
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import slide from "@/components/Slide/slide";
import Tabs from "@/components/tabs/tabs";
import SvgProple from "@/components/People/people";
export default {
components: {
XHeader,
Tab,
TabItem,
InlineLoading,
slide,
SvgProple,
Tabs
},
beforeRouteLeave(to, from, next) { // 

if (to.name == 'drugsDetails') {

localStorage.setItem('lastcodeduge_Y', this.$route.params.code || window.localStorage.lastcodeduge_Y)
} else {
localStorage.removeItem('lastcodeduge_Y');
}
next();
},
data() {
return {
tabIndex: 0,
defalutText: '--',
locationName: '',
pageIndex: 1,
pageSize: 15,
dataobj: {},
list: [














]
};
},
mounted() {
this.locationName = this.$route.params.name;
this.initdata();
},
methods: {

initdata() {
this.$ajax.post(this.$root.host, {
'MSH.1': 'diseasepool',
'MSH.2': 'getDisease',
'cbm': this.$route.params.code || window.localStorage.lastcodeduge_Y,
'token': this.$root.loginInfo.token,
}).then(data => {
console.log(data)
if (typeof data != 'undefined' && Object.keys(data).length > 0) { //

this.dataobj = data;
} else {
this.dataobj = {};
}
})
},

initdrugdata() {
this.$ajax.post(this.$root.host, {
'MSH.1': 'diseasepool',
'MSH.2': 'listDrug',
"pageIndex": this.pageIndex,
"pageSize": this.pageSize,
'cbm': this.$route.params.code || window.localStorage.lastcodeduge_Y,
'token': this.$root.loginInfo.token,
}).then(data => {
console.log(data)
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
if (typeof data != 'undefined') { //
if (typeof data.rows != 'undefined' && data.rows.length > 0) {

if (this.pageIndex == 1) {
this.list = data.rows
} else {
this.list = this.list.concat(data.rows);
}
} else {
this.judge()
}
} else {
this.judge()
}
})
},
judge() { //
if (this.pageIndex > 1) {
this.$vux.toast.text('', 'bottom');
} else {
this.list = [];
}
},
pullingDown() {
this.pageIndex = 1;
this.initdrugdata();
},
pullingUp() {
this.pageIndex++;
this.initdrugdata();
},
todrugDetail(code) {
this.$router.push({
name: 'drugsDetails',
params: {
code: code,
}
})
}
},
watch: {
tabIndex(index) {
this.$refs.slide.goToPage(index);
if (this.list.length == 0 && index == 1) {
this.initdrugdata();
}
}
}
};



