import SVG from "svg.js";
import sex from './sex.js';
import panzoom from './svg.panzoom.js';
import content from './svg.content.js';
import Hammer from 'hammerjs';
import Vivus from "vivus";

export default {
  props: {
    sex: {
      /**
       * 
       * truefalse
       */
      type: Boolean,
      default: true
    },
    face: {
      /**
       * 
       * truefalse
       */
      type: Boolean,
      default: true
    },
    lineColor: {
      /**
       * 
       * 01
       */
      type: Array,
      default: function() {
        return ["#6BBEFD", "#FD9A8E"];
      }
    },
    vivus: {
      /**
       * 
       * delayed:
       * sync:
       * oneByOne:
       */
      type: String,
      default: "delayed"
    },
    duration: {
      /**
       * 
       */
      type: Number,
      default: 150
    },
    pathTimingFunction: {
      /**
       * 
       * LINEAR EASE EASE_IN EASE_OUT EASE_OUT_BOUNCE
       */
      type: String,
      default: "LINEAR"
    },
    animTimingFunction: {
      /**
       * 
       * LINEAR EASE EASE_IN EASE_OUT EASE_OUT_BOUNCE
       */
      type: String,
      default: "LINEAR"
    },
    ease: {
      /**
       * 
       * <>: ease in and out
       *  >: ease out
       *  <: ease in
       *  -: linear
       *  =: external control
       */
      type: String,
      default: "-"
    },
    delay: {
      /**
       * 
       */
      type: String,
      default: ".3s"
    },
    delayOpacity:{
      /**
       * 
       */
      type: String,
      default: ".1s"
    },
    clickOpacity: {
      //
      type: Number,
      default:0.7
    },
    bodyPosition: {
      /**
       * 
       * :val
       *     
       *     head:
       *     neck:
       *     abdomen:()
       *     upperLimb:
       *     waist:
       *     genitals:/
       *     theLegs:
       *     
       *     wholeBody:
       */
      type: Function
    },
    isAnimation: {
      /**
       * 
       */
      type: Boolean,
      default: true
    },
    initFill:{
      type:String,
      default:'#fff'
    },
    animateEnd:{
      /**
       * 
       * 
       */
      type:Function,
      default:function(){}
    }
  },
  data() {
    return {
      isAnimate: false,
      sexData: sex,
      svgFace: this.face,
      svgSex: this.sex,
      clickBuf: null,//path
      isLabelClick: true,
    };
  },
  mounted() {
    this.svgRender();
  },
  methods: {
    decodeType() {
      let sexStr = this.svgSex ? "man" : "woman";
      let faceStr = this.svgFace ? "front" : "back";

      return [sexStr, faceStr];
    },
    //
    timingProps() {
      let timingProps = {
        type: this.vivus,
        duration: this.duration,
        start: "autostart",
        pathTimingFunction: Vivus.LINEAR,
        animTimingFunction: Vivus.LINEAR
      };

      timingProps.pathTimingFunction =
        Vivus[this.pathTimingFunction] || timingProps.pathTimingFunction;
      timingProps.animTimingFunction =
        Vivus[this.animTimingFunction] || timingProps.animTimingFunction;

      return timingProps;
    },
    svgAgainInit() {
      let self = this;
      this.isAnimate = false;
      this.draw
        .animate({delay:self.delayOpacity})
        .opacity(0)
        .afterAll(function() {
          self.draw.opacity(1);
          self.draw.remove();
          self.svgRender();
        });
    },
    svgRender() {
      let decode = this.decodeType();
      this.draw = SVG(this.$refs.svginit);
      this.draw.clear();
      this.draw.style(
        "color",
        this.svgSex ? this.lineColor[0] : this.lineColor[1]
      );

      //svg
      this.createGroup(decode[0], decode[1]);

      this.midden();

      this.isLabelClick = false;

      if (this.isAnimation) {
        this.svgSwitchVivus();
      } else {
        this.svgSwitchNo();
      }
    },
    svgSwitchVivus() {
      let prams = this.timingProps();
      let self = this;
      new Vivus(this.draw.id(), prams, function() {
        self.isAnimate = true;
        self.isLabelClick = true;
        //
        let gLabel = self.faceRect.children();

        self.animateEnd();

        gLabel.map(function(label) {
          let pathLabel = label.children();
          
          pathLabel.map(function(rect) {
            let dataAttr = rect.data("attrs");
            if (dataAttr.fill) {
              rect.attr({
                stroke: self.svgSex ? self.lineColor[0] : self.lineColor[1]
              });

              rect.animate({ ease: self.ease, delay: self.delay })
                  .attr({ fill: dataAttr.fill, stroke: dataAttr.fill });
            }
          });
        });
      });
    },
    svgSwitchNo() {
      this.isAnimate = true;
      let self = this;
      let gLabel = self.faceRect.children();

      gLabel.map(function(label) {
        let pathLabel = label.children();
        pathLabel.map(function(rect) {
          let dataAttr = rect.data("attrs");
          if (dataAttr.fill) {
            rect.attr({ fill: dataAttr.fill, stroke: dataAttr.fill });
          }
        });
      });

      this.isLabelClick = true;
    },
    //svg
    midden() {
      let clientRect = this.$refs.svginit.getBoundingClientRect();
      this.draw.width(clientRect.width);
      this.draw.height(clientRect.height);
    },
    createGroup(type, str) {
      let data = this.sexData[type];

      if (!data) {
        return;
      }

      let fronts = data[str];
      let self = this;

      let faceRect = (self.faceRect = self.draw.group());
      
      let frontKeys = Object.keys(fronts);
      frontKeys.map(function(val) {
        self.wholeBody(fronts, val, faceRect);
      });

      let faceClient = faceRect.node.getBoundingClientRect();
 
      this.draw.attr({
        'preserveAspectRatio':'xMidYMin meet',
        'viewBox':`0 0 ${faceClient.width} ${faceClient.height}`
      })
    },
    /**
     * 
     */
    wholeBody(obj, val, wood) {
      let self = this;

      let group = wood.group();
      self.createPath(group, obj[val]);

      group.node.addEventListener('touchstart',function(){
        if(!self.isLabelClick){
          return;
        }

        if(self.clickOpacity){
          group.opacity(self.clickOpacity);

          self.clickBuf = group;
        }
      })

      //
      let hammerGroup = new Hammer(group.node);
      hammerGroup.on('pressup',tapEnd);
      hammerGroup.on('tap',tapEnd);

      group.node.addEventListener('touchmove',function(){
        self.clickBuf.opacity(1);
      })

      function tapEnd(){
        if(!self.isLabelClick){
          return;
        }

        if (self.bodyPosition) {
          self.bodyPosition(val,self.clickBuf);
        }
      }
    },
    /**
     * path
     */
    createPath(group, arr) {
      const self = this;
      if (!arr && !group) {
        return;
      }

      arr.map(function(obj) {
        let path = group.path(obj.d);
        if (obj.attr) {
          path.data("attrs", obj.attr);

          let newData = { ...obj.attr };
          newData.fill = self.initFill;
          newData.stroke = "currentColor";
          path.attr(newData);
        }
      });
    }
  },
  watch: {
    face(newVal, oldVal) {
      if (this.isAnimate && newVal != this.svgFace) {
        this.svgAgainInit();
        this.svgFace = newVal;
      }
    },
    sex(newVal, oldVal) {
      if (this.isAnimate && newVal != this.svgSex) {
        this.svgAgainInit();
        this.svgSex = newVal;
      }
    }
  }
};



