;
(function () {

    SVG.extend(SVG.G, {
        /**
         * svg
         * gPosition
         * @param {*} str left right center top bottom 
         * @param {*} company 
         */
        gPosition: function (str, company) {
            company = company || 0;
            str = str || 'center';

            //svg
            //
            if (str === 'left' || str === 'right') {
                this.x(0);
            }

            if (str === 'top' || str === 'bottom') {
                this.y(0);
            }

            if (str === 'center') {
                this.x(0);
                this.y(0);
            }

            var rboxObj = this.rbox();
            var drawRboxObj = this.doc().rbox();

            if (str === 'left') {
                var px = -rboxObj.x + company;
                this.x(px);
            }

            if (str === 'right') {
                var px = drawRboxObj.width - rboxObj.x2 + company;
                this.x(px);
            }

            if (str === 'center') {
                var pxX = ((drawRboxObj.width - rboxObj.width) / 2) - rboxObj.x;
                var pxY = ((drawRboxObj.height - rboxObj.height) / 2) - rboxObj.y;

                this.x(pxX);
                this.y(pxY);
            }

            if (str === 'top') {
                var px = -rboxObj.y + company;
                this.y(px);
            }

            if (str === 'bottom') {
                var px = drawRboxObj.height - rboxObj.y2 + company;
                this.y(px);
            }

            return this;
        },
        gResize: function () {
            var rboxObj = this.rbox();
            var drawRboxObj = this.doc().rbox();
            console.log(rboxObj.height,drawRboxObj.height)
            if (rboxObj.width >= drawRboxObj.width) {
                var zoom = drawRboxObj.width / rboxObj.width;
                this.doc().zoom(zoom);
            }

            if (rboxObj.height >= drawRboxObj.height) {
                var zoom = drawRboxObj.height / rboxObj.height;
                // this.doc().zoom(zoom);
            }
        },
        /**
         * 
         */
        createLinearGradient: function (aColor, bColor) {
            aColor = aColor || '#A5CC82';
            bColor = bColor || '#00467F';

            var linear = this.gradient('linear', function (stop) {
                stop.at(0, aColor)
                stop.at(1, bColor)
            })

            linear.attr({
                'x1': '0%',
                'x2': '0%',
                'y1': '0%',
                'y2': '100%'
            })

            this.rect(20, 150).fill(linear);

            return this;
        }
    })
}())


